	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2008-02-07-secret-given-to-the-protect_from_forgery-call/index.md absPath of file >>> MarkdownRemark","html":"<p>My code was running fine under Rails 2.0.2.  Then I enabled the db based session store by uncommenting this:</p>\n<pre><code class=\"ruby\">\n  config.action_controller.session_store = :active_record_store\n</pre></code>\n<p>in environment.rb.</p>\n<p>Anything that used the session after that gave me this error:</p>\n<pre><code class=\"ruby\">\nNo :secret given to the #protect_from_forgery call.  Set that or use a session store capable of generating its own keys (Cookie Session Store).\n</pre></code>\n<p>If you have the same problem.  Go to application.rb and uncomment the secret then restart.</p>\n<p>All better.</p>","frontmatter":{"title":"secret given to the #protect_from_forgery call","date":"February 07, 2008","imageUrl":null,"structuredHtml":null,"image":null}}},"pathContext":{}}

